import React, { FC, useState } from 'react'
import { RouteComponentProps } from '@reach/router'
import { useForm } from 'react-hook-form'
import { ArrowLeftIcon, CheckIcon, FilterIcon } from '@heroicons/react/outline'
import Form from '@primer/components/Form/Form'
import FormInput from '@primer/components/Form/FormInput'
import Actions from '@primer/components/Actions'
import PageContent from '@primer/components/PageContent'
import PageHeading from '@primer/components/PageHeading'
import Card, { CardWrapper } from '@primer/components/Card'
import Stack from '@primer/components/Stack'
import FormCheckbox from '@primer/components/Form/FormCheckbox'
import clsx from 'clsx'
import Checkbox from '@primer/components/Checkbox'
import CardHeader from '@primer/components/CardHeader'

const userPermissions = [
	{ name: 'createUser', label: 'Create User' },
	{ name: 'viewUserDetails', label: 'View User Details' },
	{ name: 'updateUserDetails', label: 'Update User Details' },
	{ name: 'viewUserRole', label: 'View User Role' },
	{ name: 'updateUserRole', label: 'Update User Role' },
	{ name: 'deleteUser', label: 'Delete User' },
	{ name: 'viewUserCases', label: 'View User Cases' },
	{ name: 'viewUserNotifications', label: 'View User Notifications' },
]

const casePermissions = [
	{ name: 'createCase', label: 'Create Case' },
	{ name: 'viewCaseDetails', label: 'View Case Details' },
	{ name: 'updateCaseDetails', label: 'Update Case Details' },
	{ name: 'updateCaseRole', label: 'Update Case Role' },
	{ name: 'deleteCase', label: 'Delete Case' },
]

const eventPermissions = [
	{ name: 'createEvent', label: 'Create Event' },
	{ name: 'updateEventDetails', label: 'Update Event Details' },
	{ name: 'updateEventRole', label: 'Update Event Role' },
	{ name: 'deleteEvent', label: 'Delete Event' },
	{ name: 'addEvent', label: 'Delete Event' },
]

interface PermissionSet {
	[x: string]: boolean
}

interface FormData {
	name: string
	permissions: PermissionSet
}

const CreateRole: FC<RouteComponentProps> = () => {
	const formContext = useForm<FormData>({
		defaultValues: {
			name: '',
			permissions: {
				...userPermissions.reduce(
					(perms, { name }) => ({
						...perms,
						[name]: !!Math.floor(Math.random() * 2),
					}),
					{}
				),
				...casePermissions.reduce(
					(perms, { name }) => ({
						...perms,
						[name]: !!Math.floor(Math.random() * 2),
					}),
					{}
				),
				...eventPermissions.reduce(
					(perms, { name }) => ({
						...perms,
						[name]: !!Math.floor(Math.random() * 2),
					}),
					{}
				),
			},
		},
	})

	const permissions = formContext.watch('permissions')

	const [userPermissionsFilter, setUserPermissionsFilter] = useState('')
	const [eventPermissionsFilter, setEventPermissionsFilter] = useState('')
	const [casePermissionsFilter, setCasePermissionsFilter] = useState('')

	return (
		<>
			<PageHeading
				pageTitle="Create Role"
				title={
					<div className="flex items-center space-x-3">
						<Actions
							actions={{
								icon: <ArrowLeftIcon className="w-5 h-5 -mx-2" />,
								intent: 'borderless',
								rounded: 'full',
								to: '/settings/roles',
							}}
						/>
						<div>Create Role</div>
					</div>
				}
			/>

			<PageContent>
				<Form context={formContext} onSubmit={() => {}}>
					<Stack>
						<Card
							header={<CardHeader title="Details" subtitle="Basic details about the role"></CardHeader>}
						>
							<Stack>
								<FormInput
									name="name"
									label="Name"
									validations={{ required: 'Name is required' }}
									className="w-[300px]"
								/>
							</Stack>
						</Card>

						<CardWrapper>
							<CardHeader title="Permissions" subtitle="What is the role allowed to do" />

							<div className="py-6 px-2 flex flex-col sm:flex-row space-y-6 sm:space-y-0 divide-x divide-gray-200">
								<div className="flex-1 px-4">
									<Stack>
										<div className="text-lg">Users</div>
										<div className="relative">
											<input
												className="form-input block w-full sm:text-xs sm:leading-4 pl-8"
												type="text"
												value={userPermissionsFilter}
												onChange={(e) => setUserPermissionsFilter(e.target.value)}
											/>
											<FilterIcon className="w-4 h-4 absolute top-0 left-0 mt-2.5 ml-2.5 text-gray-400" />
										</div>
										<div>
											<div className="mb-4 pb-3 border-b border-gray-100">
												<Checkbox
													checked={Object.entries(permissions)
														.filter(
															([name]) => !!userPermissions.find((x) => x.name === name)
														)
														.reduce<boolean>((bool, [_, val]) => bool && val, true)}
													onChange={(e) =>
														formContext.setValue('permissions', {
															...permissions,
															...userPermissions.reduce(
																(perms, { name }) => ({
																	...perms,
																	[name]: e.target.checked,
																}),
																{}
															),
														})
													}
													label="Check All"
												/>
											</div>

											{userPermissions.map((x, i) => (
												<FormCheckbox
													key={i}
													name={`permissions.${x.name}`}
													label={x.label}
													className={clsx(
														x.label
															.toLowerCase()
															.indexOf(userPermissionsFilter.toLowerCase()) > -1
															? 'mb-6 last:mb-0'
															: 'hidden'
													)}
												/>
											))}
										</div>
									</Stack>
								</div>

								<div className="flex-1 px-4">
									<Stack>
										<div className="text-lg">Cases</div>
										<div className="relative">
											<input
												className="form-input block w-full sm:text-xs sm:leading-4 pl-8"
												type="text"
												value={casePermissionsFilter}
												onChange={(e) => setCasePermissionsFilter(e.target.value)}
											/>
											<FilterIcon className="w-4 h-4 absolute top-0 left-0 mt-2.5 ml-2.5 text-gray-400" />
										</div>
										<div>
											<div className="mb-4 pb-3 border-b border-gray-100">
												<Checkbox
													checked={Object.entries(permissions)
														.filter(
															([name]) => !!casePermissions.find((x) => x.name === name)
														)
														.reduce<boolean>((bool, [_, val]) => bool && val, true)}
													onChange={(e) =>
														formContext.setValue('permissions', {
															...permissions,
															...casePermissions.reduce(
																(perms, { name }) => ({
																	...perms,
																	[name]: e.target.checked,
																}),
																{}
															),
														})
													}
													label="Check All"
												/>
											</div>
											{casePermissions.map((x, i) => (
												<FormCheckbox
													key={i}
													name={`permissions.${x.name}`}
													label={x.label}
													className={clsx(
														x.label
															.toLowerCase()
															.indexOf(casePermissionsFilter.toLowerCase()) > -1
															? 'mb-6 last:mb-0'
															: 'hidden'
													)}
												/>
											))}
										</div>
									</Stack>
								</div>

								<div className="flex-1 px-4">
									<Stack>
										<div className="text-lg">Events</div>
										<div className="relative">
											<input
												className="form-input block w-full sm:text-xs sm:leading-4 pl-8"
												type="text"
												value={eventPermissionsFilter}
												onChange={(e) => setEventPermissionsFilter(e.target.value)}
											/>
											<FilterIcon className="w-4 h-4 absolute top-0 left-0 mt-2.5 ml-2.5 text-gray-400" />
										</div>
										<div>
											<div className="mb-4 pb-3 border-b border-gray-100">
												<Checkbox
													checked={Object.entries(permissions)
														.filter(
															([name]) => !!eventPermissions.find((x) => x.name === name)
														)
														.reduce<boolean>((bool, [_, val]) => bool && val, true)}
													onChange={(e) =>
														formContext.setValue('permissions', {
															...permissions,
															...eventPermissions.reduce(
																(perms, { name }) => ({
																	...perms,
																	[name]: e.target.checked,
																}),
																{}
															),
														})
													}
													label="Check All"
												/>
											</div>
											{eventPermissions.map((x, i) => (
												<FormCheckbox
													key={i}
													name={`permissions.${x.name}`}
													label={x.label}
													className={clsx(
														x.label
															.toLowerCase()
															.indexOf(eventPermissionsFilter.toLowerCase()) > -1
															? 'mb-6 last:mb-0'
															: 'hidden'
													)}
												/>
											))}
										</div>
									</Stack>
								</div>
							</div>
						</CardWrapper>

						<div className="flex items-center justify-end">
							<Actions
								actions={[
									{
										title: 'Create',
										intent: 'primary',
										icon: <CheckIcon className="w-5 h-5" />,
										type: 'submit',
									},
								]}
							/>
						</div>
					</Stack>
				</Form>
			</PageContent>
		</>
	)
}

export default CreateRole
