import { EntityActionModel, EntityTypeModel, MetaTypeModel } from 'types/models'
import { v4 as id } from 'uuid'
import data, { clear, persist } from './index'

export const seed = () => {
	clear()
	persist()

	// ENTITY TYPES

	const userEntityType: EntityTypeModel = {
		id: id(),
		key: 'user',
		name: 'User',
		plural: 'Users',
	}

	const caseEntityType: EntityTypeModel = {
		id: id(),
		key: 'case',
		name: 'Case',
		plural: 'Cases',
	}

	const eventEntityType: EntityTypeModel = {
		id: id(),
		key: 'event',
		name: 'Event',
		plural: 'Events',
	}

	data.entityTypes.save(userEntityType)
	data.entityTypes.save(caseEntityType)
	data.entityTypes.save(eventEntityType)

	// META TYPES

	// CASE

	const caseMetaTypes: MetaTypeModel[] = [
		{
			id: id(),
			entityTypeId: caseEntityType.id,
			key: 'case-number',
			name: 'Case Number',
			type: 'string',
			options: [],
		},
		{
			id: id(),
			entityTypeId: caseEntityType.id,
			key: 'type',
			name: 'Type',
			type: 'option',
			options: ['Review', 'Mediation', 'Hybrid'],
			defaultValue: 'Review',
		},
		{
			id: id(),
			entityTypeId: caseEntityType.id,
			key: 'claim-number',
			name: 'Claim Number',
			type: 'string',
			options: [],
		},
		{
			id: id(),
			entityTypeId: caseEntityType.id,
			key: 'complexity',
			name: 'Complexity',
			type: 'option',
			options: ['Standard', 'Complex'],
		},
		{
			id: id(),
			entityTypeId: caseEntityType.id,
			key: 'closed-date',
			name: 'Closed Date',
			type: 'date',
			options: [],
		},
	]

	caseMetaTypes.forEach(data.metaTypes.save)

	// EVENTS

	const eventMetaTypes: MetaTypeModel[] = [
		{
			id: id(),
			entityTypeId: eventEntityType.id,
			key: 'start-date',
			name: 'Start Date',
			type: 'date',
			options: [],
		},
		{
			id: id(),
			entityTypeId: eventEntityType.id,
			key: 'end-date',
			name: 'End Date',
			type: 'date',
			options: [],
		},
		{
			id: id(),
			entityTypeId: eventEntityType.id,
			key: 'type',
			name: 'Type',
			type: 'option',
			options: ['Review', 'Mediation', 'CMC'],
			defaultValue: 'CMC',
		},
		{
			id: id(),
			entityTypeId: eventEntityType.id,
			key: 'decision-date',
			name: 'Decision Date',
			type: 'date',
			options: [],
		},
		{
			id: id(),
			entityTypeId: eventEntityType.id,
			key: 'postponed',
			name: 'Postponed',
			type: 'boolean',
			options: [],
		},
	]

	eventMetaTypes.forEach(data.metaTypes.save)

	// USERS

	const userMetaTypes: MetaTypeModel[] = [
		{
			id: id(),
			entityTypeId: userEntityType.id,
			key: 'first-name',
			name: 'First Name',
			type: 'string',
			options: [],
		},
		{
			id: id(),
			entityTypeId: userEntityType.id,
			key: 'last-name',
			name: 'Last Name',
			type: 'string',
			options: [],
		},
		{
			id: id(),
			entityTypeId: userEntityType.id,
			key: 'email',
			name: 'Email',
			type: 'string',
			options: [],
		},
		{
			id: id(),
			entityTypeId: userEntityType.id,
			key: 'phone',
			name: 'Phone',
			type: 'string',
			options: [],
		},
		{
			id: id(),
			entityTypeId: userEntityType.id,
			key: 'active',
			name: 'Active',
			type: 'boolean',
			options: [],
			defaultValue: 'true',
		},
	]

	userMetaTypes.forEach(data.metaTypes.save)

	// ACTIONS

	// CASE

	const caseActions: EntityActionModel[] = [
		{
			id: id(),
			entityTypeId: caseEntityType.id,
			key: 'close-case',
			name: 'Close Case',
			changes: [],
		},
	]

	caseActions.forEach(data.entityActions.save)

	// EVENTS

	const eventActions: EntityActionModel[] = []

	eventActions.forEach(data.entityActions.save)

	// CONNECTIONS

	data.entityConnections.save({
		id: id(),
		key: 'events',
		name: 'Events',
		entityTypeId: caseEntityType.id,
		relationId: eventEntityType.id,
		type: 'one-to-many',
	})

	data.entityConnections.save({
		id: id(),
		key: 'attendees',
		name: 'Attendees',
		entityTypeId: eventEntityType.id,
		relationId: userEntityType.id,
		type: 'one-to-many',
	})

	data.entityConnections.save({
		id: id(),
		key: 'applicant',
		name: 'Applicant',
		entityTypeId: userEntityType.id,
		relationId: caseEntityType.id,
		type: 'one-to-many',
	})

	// NOTIFICATIONS

	data.notifications.save({
		id: id(),
		name: 'Event Reminder 2 days',
		focus: eventEntityType.id,
		enabled: true,
		triggerType: 'date',
		triggerRepeat: 2,
		triggerEvery: '',
		triggerYearIn: '',
		triggerMonthOn: '',
		triggerWeekOn: '',
		triggerDayAt: '',
		triggerAction: '',
		obeyTriggerTiming: true,
		triggerTimingType: 'before',
		triggerTiming: 'day',
		triggerDate:
			data.metaTypes.find((x) => x.entityTypeId === eventEntityType.id && x.key === 'start-date')?.id || '',
		ruleGroups: [
			{
				rules: [
					{
						entity: eventEntityType.id,
						field:
							data.metaTypes.find((x) => x.entityTypeId === eventEntityType.id && x.key === 'postponed')
								?.id || '',
						operator: 'false',
						value: '',
					},
				],
			},
		],
		subject: 'Upcoming Conference',
		content:
			'<h1>Hello {{recipient-first-name}}</h1><p><br></p><p>You have a {{event-type}} coming up on {{event-start-date}}</p>',
	})

	data.templates.save({
		id: id(),
		name: 'Event Reminder 2 days',
		content:
			'<h1>Hello {{recipient-first-name}}</h1><p><br></p><p>You have a {{event-type}} coming up on {{event-start-date}}</p>',
	})
}

if (!localStorage.getItem('data')) {
	seed()
}
