import {
	EntityTypeModel,
	MetaTypeModel,
	EntityActionModel,
	EntityConnectionModel,
	Unsaved,
	isUnsaved,
	NotificationModel,
	TemplateModel,
} from 'types/models'
import { v4 as uuid } from 'uuid'

interface Storage {
	entityTypes: EntityTypeModel[]
	metaTypes: MetaTypeModel[]
	entityActions: EntityActionModel[]
	entityConnections: EntityConnectionModel[]
	notifications: NotificationModel[]
	templates: TemplateModel[]
}

const storage: Storage = {
	entityTypes: [],
	metaTypes: [],
	entityActions: [],
	entityConnections: [],
	notifications: [],
	templates: [],
}

export const renderContainer = {
	current: () => {},
}

const dataActions = <T extends Storage[K][0], K extends keyof Storage>(prop: K) => ({
	all: (filter?: (x: T) => boolean): T[] => {
		if (filter && storage[prop]) return Array.prototype.filter.call(storage[prop], filter)
		// @ts-ignore
		return storage[prop] || []
	},
	find: (id?: string | ((x: T) => boolean)): T | undefined => {
		return Array.prototype.find.call(storage[prop], typeof id === 'function' ? id : (x) => x.id === id)
	},
	save: (data: T | Unsaved<T>) => {
		if (isUnsaved<T>(data)) {
			let x = {
				id: uuid(),
				...data,
			}
			Array.prototype.push.call(storage[prop], x)
			persist()
			return x
		} else {
			let added = false

			// @ts-ignore
			storage[prop] = storage[prop].map((x) => {
				// @ts-ignore
				if (x.id === data.id) {
					added = true
					return data
				}
				return x
			})

			if (!added) Array.prototype.push.call(storage[prop], data)

			persist()
			return data
		}
	},
	remove: (id: string) => {
		// @ts-ignore
		storage[prop] = storage[prop].filter((x) => x.id !== id)
		persist()
		renderContainer.current()
	},
})

const data = {
	entityTypes: dataActions('entityTypes'),
	metaTypes: dataActions('metaTypes'),
	entityActions: dataActions('entityActions'),
	entityConnections: dataActions('entityConnections'),
	notifications: dataActions('notifications'),
	templates: dataActions('templates'),
}

export const persist = () => localStorage.setItem('data', JSON.stringify(storage))

export const restore = () => {
	const data = JSON.parse(localStorage.getItem('data') || 'null')

	if (data) {
		storage.entityTypes = data.entityTypes
		storage.metaTypes = data.metaTypes
		storage.entityActions = data.entityActions
		storage.entityConnections = data.entityConnections
		storage.notifications = data.notifications
		storage.templates = data.templates
	}
}

export const clear = () => {
	localStorage.removeItem('data')

	storage.entityTypes = []
	storage.metaTypes = []
	storage.entityActions = []
	storage.entityConnections = []
	storage.notifications = []
	storage.templates = []
}

restore()

export default data
