import React, { FC } from 'react'
import PageHeading from '@primer/components/PageHeading'
import PageNavbar from '@primer/components/PageNavbar'
import { RouteComponentProps } from '@reach/router'
import Table, { TableSchema } from '@primer/components/Table'
import { BeakerIcon, DotsHorizontalIcon, PencilIcon, PlusIcon, TrashIcon } from '@heroicons/react/outline'
import { links } from '../Settings'
import SectionHeader from '@primer/components/SectionHeader'
import Stack from '@primer/components/Stack'
import { TemplateModel } from 'types/models'
import data from 'data'

const Templates: FC<RouteComponentProps> = () => {
	const deleteNotification = (x: TemplateModel) => null

	const schema: TableSchema<TemplateModel> = {
		cols: [
			{
				title: 'Name',
				value: (x) => x.name,
			},
		],
		actions: (x) => [
			{
				icon: <DotsHorizontalIcon className="w-5 h-5" />,
				intent: 'menu',
				actions: [
					[
						{
							title: 'Edit',
							icon: <PencilIcon className="w-5 h-5" />,
							to: `${x.id}/edit`,
						},
						{
							title: 'Test',
							icon: <BeakerIcon className="w-5 h-5" />,
							onClick: () => {},
							disabled: true,
						},
					],
					[
						{
							title: 'Delete',
							intent: 'danger',
							icon: <TrashIcon className="w-5 h-5" />,
							onClick: () => deleteNotification(x),
						},
					],
				],
			},
		],
	}

	return (
		<>
			<PageHeading title="Settings" />

			<PageNavbar links={links}>
				<Stack>
					<div className="px-6 pt-6">
						<SectionHeader
							title="Templates"
							actions={[
								{
									title: 'Create Template',
									icon: <PlusIcon className="w-5 h-5" />,
									intent: 'primary',
									to: '/settings/templates/create',
								},
							]}
						/>
					</div>
					<Table schema={schema} items={data.templates.all()} className="-mb-px" />
				</Stack>
			</PageNavbar>
		</>
	)
}

export default Templates
