import React, { FC } from 'react'
import { v4 as uuid } from 'uuid'
import Card, { CardWrapper } from '@primer/components/Card'
import PageHeading from '@primer/components/PageHeading'
import PageContent from '@primer/components/PageContent'
import { navigate, RouteComponentProps } from '@reach/router'
import { ArrowLeftIcon, CheckIcon, PencilIcon, PlusIcon, XIcon } from '@heroicons/react/outline'
import Actions from '@primer/components/Actions'
import Stack from '@primer/components/Stack'
import Form, { useForm } from '@primer/components/Form2/Form'
import FormInput from '@primer/components/Form2/FormInput'
import data from 'data'
import { EntityActionModel, EntityConnectionModel, MetaTypeModel } from 'types/models'
import useFieldArray from '@primer/components/Form2/useFieldArray'
import FormSection from '@primer/components/Form2/FormSection'
import Table, { TableSchema } from '@primer/components/Table'
import openMetaType, { typeOptions } from 'slideouts/openMetaType'
import openEntityAction from 'slideouts/openEntityAction'
import { confirm } from '@primer/alerts'
import { slugify } from '@primer/utils/funcs'
import openEntityConnection, { connectionTypeOptions } from 'slideouts/openEntityConnection'

interface FormSchema {
	name: string
	plural: string
	metaTypes: MetaTypeModel[]
	entityActions: EntityActionModel[]
	entityConnections: EntityConnectionModel[]
}

const EditEntityType: FC<RouteComponentProps<{ id: string; mode: 'create' | 'edit' }>> = ({ id, mode }) => {
	const original = data.entityTypes.find(id)
	const originalMetaTypes = data.metaTypes.all((x) => x.entityTypeId === id)
	const originalEntityActions = data.entityActions.all((x) => x.entityTypeId === id)
	const originalEntityConnections = data.entityConnections.all((x) => x.entityTypeId === id)

	const formContext = useForm<FormSchema>({
		initialValues: {
			name: original?.name || '',
			plural: original?.plural || '',
			metaTypes: originalMetaTypes || [],
			entityActions: originalEntityActions || [],
			entityConnections: originalEntityConnections || [],
		},
		onSubmit: (formSchema) => {
			const newEntityType = data.entityTypes.save({
				...original,
				key: slugify(formSchema.name),
				name: formSchema.name,
				plural: formSchema.plural,
			})

			formSchema.metaTypes.forEach((x) => {
				data.metaTypes.save({
					...x,
					entityTypeId: newEntityType.id,
				})
			})

			formSchema.entityActions.forEach((x) => {
				data.entityActions.save({
					...x,
					entityTypeId: newEntityType.id,
				})
			})

			formSchema.entityConnections.forEach((x) => {
				data.entityConnections.save({
					...x,
					entityTypeId: newEntityType.id,
				})
			})

			navigate('/settings/entities')
		},
	})

	const { values: metaTypes, arrayHelpers: metaTypeHelpers } = useFieldArray('metaTypes', formContext)
	const { values: entityActions, arrayHelpers: entityActionsHelpers } = useFieldArray('entityActions', formContext)
	const { values: entityConnections, arrayHelpers: entityConnectionsHelpers } = useFieldArray(
		'entityConnections',
		formContext
	)

	const metaTypeSchema: TableSchema<MetaTypeModel> = {
		cols: [
			{
				title: 'Name',
				value: (x) => x.name,
			},
			{
				title: 'Type',
				value: (x) => typeOptions.find((y) => y.value === x.type)?.label || '',
			},
		],
		actions: (x, i) => [
			{
				icon: <PencilIcon className="w-5 h-5" />,
				intent: 'menu',
				onClick: async () => {
					const editedMetaType = await openMetaType({ mode: 'edit', data: x })
					if (editedMetaType) metaTypeHelpers.replace(i, editedMetaType)
				},
			},
			{
				icon: <XIcon className="w-5 h-5" />,
				intent: 'menu-danger',
				onClick: () => {
					confirm({
						title: 'Are you sure?',
						message: 'This cannot be undone',
						onAccept: () => {
							metaTypeHelpers.remove(i)
						},
					})
				},
			},
		],
	}

	const entityActionsSchema: TableSchema<EntityActionModel> = {
		cols: [
			{
				title: 'Name',
				value: (x) => x.name,
			},
		],
		actions: (x, i) => [
			{
				icon: <PencilIcon className="w-5 h-5" />,
				intent: 'menu',
				onClick: async () => {
					const editedEntityAction = await openEntityAction({ mode: 'edit', data: x, metaTypes })
					if (editedEntityAction) entityActionsHelpers.replace(i, editedEntityAction)
				},
			},
			{
				icon: <XIcon className="w-5 h-5" />,
				intent: 'menu-danger',
				onClick: () => {
					confirm({
						title: 'Are you sure?',
						message: 'This cannot be undone',
						onAccept: () => {
							entityActionsHelpers.remove(i)
						},
					})
				},
			},
		],
	}

	const connectionSchema: TableSchema<EntityConnectionModel> = {
		cols: [
			{
				title: 'Name',
				value: (x) => x.name,
			},
			{
				title: 'Entity',
				value: (x) => data.entityTypes.find((y) => y.id === x.relationId)?.name,
			},
			{
				title: 'Cardinality',
				value: (x) => connectionTypeOptions.find((y) => y.value === x.type)?.label,
			},
		],
		actions: (x, i) => [
			{
				icon: <PencilIcon className="w-5 h-5" />,
				intent: 'menu',
				onClick: async () => {
					const editedEntityConnection = await openEntityConnection({ mode: 'edit', data: x })
					if (editedEntityConnection) entityActionsHelpers.replace(i, editedEntityConnection)
				},
			},
			{
				icon: <XIcon className="w-5 h-5" />,
				intent: 'menu-danger',
				onClick: () => {
					confirm({
						title: 'Are you sure?',
						message: 'This cannot be undone',
						onAccept: () => {
							entityConnectionsHelpers.remove(i)
						},
					})
				},
			},
		],
	}

	const title = mode === 'create' ? 'Create Entity Type' : 'Edit Entity Type'

	return (
		<>
			<PageHeading
				pageTitle={title}
				title={
					<div className="flex items-center space-x-3">
						<Actions
							actions={{
								icon: <ArrowLeftIcon className="w-5 h-5 -mx-2" />,
								intent: 'borderless',
								rounded: 'full',
								to: '/settings/entities',
							}}
						/>
						<div>{title}</div>
					</div>
				}
			/>

			<Form context={formContext}>
				<PageContent>
					<Stack dividers>
						<FormSection title="Details" subtitle="The name and different types of labels for the entity">
							<Card>
								<Stack>
									<FormInput
										name="name"
										label="Name"
										validations={{ required: 'Name is required' }}
									/>

									<FormInput name="plural" label="Plural" />
								</Stack>
							</Card>
						</FormSection>

						<FormSection
							title="Field Types"
							subtitle="Field Types are custom fields that are attached to this entity, that can be configured to contain many different kinds of data"
						>
							<Stack>
								<CardWrapper className="rounded-md overflow-hidden">
									<Table items={metaTypes} schema={metaTypeSchema} className="-my-px" />
								</CardWrapper>

								<div className="flex justify-end">
									<Actions
										actions={{
											title: 'Add Field Type',
											icon: <PlusIcon className="w-5 h-5" />,
											intent: 'invisible',
											onClick: async () => {
												const newMetaType = await openMetaType({
													data: {
														id: uuid(),
														entityTypeId: '',
														key: '',
														name: '',
														plural: '',
														template: '',
														type: 'string',
														options: [''],
													},
												})

												if (newMetaType) metaTypeHelpers.push(newMetaType)
											},
										}}
									/>
								</div>
							</Stack>
						</FormSection>

						<FormSection
							title="Actions"
							subtitle="Actions are custom interactions that the user can use do to this entity. They can modify a Meta Value, or be used as triggers for notifications."
						>
							<Stack>
								<CardWrapper className="rounded-md overflow-hidden">
									<Table items={entityActions} schema={entityActionsSchema} className="-my-px" />
								</CardWrapper>

								<div className="flex justify-end">
									<Actions
										actions={{
											title: 'Add Action',
											icon: <PlusIcon className="w-5 h-5" />,
											intent: 'invisible',
											onClick: async () => {
												const newEntityAction = await openEntityAction({
													data: {
														id: uuid(),
														entityTypeId: '',
														key: '',
														name: '',
														changes: [],
													},
													metaTypes,
												})

												if (newEntityAction) entityActionsHelpers.push(newEntityAction)
											},
										}}
									/>
								</div>
							</Stack>
						</FormSection>

						<FormSection
							title="Connections"
							subtitle={
								<div className="space-y-3">
									<p>
										Connections are a way to define a relationship between this entity type, and
										another entity type.
									</p>
									<p>
										For example, if a "case has many events", then a case should have a connection
										named "Events", set to the Event entity and a cardinality of "Many"
									</p>
								</div>
							}
						>
							<Stack>
								<CardWrapper className="rounded-md overflow-hidden">
									<Table items={entityConnections} schema={connectionSchema} className="-my-px" />
								</CardWrapper>

								<div className="flex justify-end">
									<Actions
										actions={{
											title: 'Add Connection',
											icon: <PlusIcon className="w-5 h-5" />,
											intent: 'invisible',
											onClick: async () => {
												const newConnection = await openEntityConnection({
													data: {
														id: uuid(),
														key: '',
														name: '',
														entityTypeId: '',
														relationId: '',
														type: 'one-to-many',
													},
												})

												if (newConnection) entityConnectionsHelpers.push(newConnection)
											},
										}}
									/>
								</div>
							</Stack>
						</FormSection>

						<div className="flex justify-end">
							<Actions
								actions={[
									{
										title: 'Submit',
										intent: 'save',
										icon: <CheckIcon className="w-5 h-5" />,
										type: 'submit',
									},
								]}
							/>
						</div>
					</Stack>
				</PageContent>
			</Form>
		</>
	)
}

export default EditEntityType
