import React, { FC } from 'react'
import openSlideout, { SlideoutHeader, SlideoutContent } from '@primer/hooks/openSlideout'
import Form, { useForm } from '@primer/components/Form2/Form'
import Stack from '@primer/components/Stack'
import { ConnectionType, EntityConnectionModel, RoleModel, Unsaved } from 'types/models'
import { CheckIcon } from '@heroicons/react/outline'
import Actions from '@primer/components/Actions'
import { SlideoutResolveFn } from '@primer/types'
import FormSelect from '@primer/components/Form2/FormSelect'
import db from 'data'
import { slugify } from '@primer/utils/funcs'
import FormInput from '@primer/components/Form2/FormInput'
import Table, { TableSchema } from '@primer/components/Table'
import FormLabel from '@primer/components/Form2/FormLabel'
import TableFrame from '@primer/components/TableFrame'
import { roles } from 'fakedata'

export const connectionTypeOptions = [
	{ value: 'one-to-one', label: 'Single' },
	{ value: 'one-to-many', label: 'Many' },
]

interface OpenEntityConnectionProps {
	mode?: 'create' | 'edit'
	data: EntityConnectionModel
}

const openEntityConnection = (props: OpenEntityConnectionProps) => {
	return openSlideout<EntityConnectionModel | Unsaved<EntityConnectionModel>>({
		render: (close) => <Component close={close} {...props} />,
	})
}

interface FormSchema {
	name: string
	entityTypeId: string
	relationId: string
	type: ConnectionType
}

const Component: FC<
	OpenEntityConnectionProps & { close: SlideoutResolveFn<EntityConnectionModel | Unsaved<EntityConnectionModel>> }
> = ({ mode = 'create', data, close }) => {
	const formContext = useForm<FormSchema>({
		initialValues: data as FormSchema,
		onSubmit: (formData) => {
			close({
				...formData,
				key: slugify(formData.name),
			})
		},
	})

	const permissionsSchema: TableSchema<RoleModel> = {
		cols: [
			{
				title: 'Role',
				value: (x) => x.name,
			},
			{
				title: 'View',
				value: (_, i) => (
					<div className="w-full flex justify-center">
						<input
							type="checkbox"
							className="form-checkbox text-primary-500"
							checked={true}
							onChange={() => {}}
						/>
					</div>
				),
				width: 'minmax(auto, max-content)',
			},
			{
				title: 'Modify',
				value: (_, i) => (
					<div className="w-full flex justify-center">
						<input
							type="checkbox"
							className="form-checkbox text-primary-500"
							checked={true}
							onChange={() => {}}
						/>
					</div>
				),
				width: 'minmax(auto, max-content)',
			},
		],
	}

	return (
		<Form context={formContext} className="contents">
			<SlideoutHeader title={mode === 'edit' ? 'Edit Connection' : 'Create Connection'} />

			<SlideoutContent>
				<Stack className="py-6">
					<div className="px-6">
						<Stack>
							<FormInput name="name" label="Name" validations={{ required: 'Name is required' }} />

							<FormSelect
								name="relationId"
								label="Entity Type"
								options={db.entityTypes.all().map((x) => ({ value: x.id, label: x.name }))}
								validations={{ required: 'Entity Type is required' }}
							/>

							<FormSelect
								name="type"
								label="Cardinality"
								options={connectionTypeOptions}
								validations={{ required: 'Cardinality is required' }}
							/>

							<div>
								<FormLabel>Permissions</FormLabel>
								<TableFrame className="mt-2">
									<Table schema={permissionsSchema} items={roles} className="-my-px" />
								</TableFrame>
							</div>
						</Stack>
					</div>
				</Stack>
			</SlideoutContent>

			<div className="p-6 border-t border-gray-200 dark:border-gray-600 bg-gray-100 dark:bg-gray-750 flex justify-end">
				<Actions
					actions={[
						{
							title: 'Cancel',
							onClick: () => close(),
						},
						{
							title: 'Save',
							icon: <CheckIcon className="w-5 h-5" />,
							intent: 'save',
							type: 'submit',
						},
					]}
				/>
			</div>
		</Form>
	)
}

export default openEntityConnection
