export type Unsaved<T> = Omit<T, 'id'>

export const isUnsaved = <T>(x: any): x is Unsaved<T> => typeof x === 'object' && typeof x.id === 'undefined'
export const isSaved = <T>(x: any): x is Unsaved<T> => typeof x === 'object' && typeof x.id === 'string'

export interface Model {
	id: string
}

export interface UserModel extends Model {
	firstName: string
	lastName: string
	fullName: string
}

export interface RoleModel extends Model {
	name: string
}

export interface EntityTypeModel extends Model {
	key: string
	name: string
	plural?: string
}

export interface EntityModel extends Model {
	entityTypeId: string
}

export type MetaType = 'string' | 'number' | 'date' | 'boolean' | 'option' | 'entityType' | 'entity'

export interface MetaTypeModel extends Model {
	entityTypeId: string
	key: string
	name: string
	plural?: string
	template?: string
	type: MetaType
	options: string[]
	defaultValue?: string
}

export interface MetaModel extends Model {
	metaTypeId: string
	entityId: string
	value: any
}

export interface EntityActionModel extends Model {
	entityTypeId: string
	key: string
	name: string
	changes?: EntityActionChangeModel[]
}

export interface EntityActionChangeModel extends Model {
	entityActionId: string
	metaTypeId: string
	userDefined: boolean
}

export type ConnectionType = 'one-to-one' | 'one-to-many' | 'many-to-many'

export interface EntityConnectionModel extends Model {
	key: string
	name: string
	entityTypeId: string
	relationId: string
	type: ConnectionType
}

export interface NotificationModel extends Model {
	name: string
	focus: string
	enabled: boolean
	enabledDate?: Date
	triggerType: string
	triggerRepeat: number
	triggerEvery: string
	triggerYearIn: string
	triggerMonthOn: string
	triggerWeekOn: string
	triggerDayAt: string
	triggerAction: string
	obeyTriggerTiming: boolean
	triggerTimingType: string
	triggerTiming: string
	triggerDate: string
	subject: string
	content: string
	ruleGroups: NotificationRuleGroup[]
}

export interface NotificationRuleGroup {
	rules: NotificationRule[]
}

export interface NotificationRule {
	entity: string
	field: string
	operator: string
	value: string
}

export interface TemplateModel {
	id: string
	name: string
	content: string
}
